/* Существующие стили */
.app-container {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.content-container {
  flex: 1;
  overflow-y: auto;
}

.main-content {
  padding: 20px;
}

.sleep-nav {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
}

.nav-button {
  background: none;
  border: none;
  color: #666;
  font-size: 14px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nav-button.active {
  color: #F5C563;
}

.nav-button svg {
  margin-bottom: 5px;
}

.sleep-content {
  padding: 10px;
}

.section-title {
  font-size: 20px;
  margin-bottom: 10px;
  color: #333;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
  margin-bottom: 20px;
}

.story-card {
  background-size: cover;
  border-radius: 10px;
  padding: 15px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 150px;
  position: relative;
}

.story-info {
  color: white;
  z-index: 1;
}

.story-title {
  font-size: 16px;
  margin: 0;
}

.story-author {
  font-size: 12px;
  margin: 5px 0 0;
}

.play-icon {
  position: absolute;
  bottom: 10px;
  right: 10px;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 50%;
  padding: 5px;
  border: none;
  cursor: pointer;
}

.play-icon svg {
  color: #333;
}

.button-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 20px;
}

.sleep-button {
  padding: 10px 20px;
  background-color: #F5C563;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.sleep-button:hover {
  background-color: #E5989B;
}

.subscription-info {
  font-size: 14px;
  color: #333;
  text-align: center;
}

/* Плеер */
.player-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.player-container {
  background: white;
  border-radius: 10px;
  padding: 20px;
  width: 90%;
  max-width: 400px;
  text-align: center;
  position: relative;
}

.player-cover {
  height: 150px;
  border-radius: 10px;
  margin-bottom: 10px;
}

.player-title {
  font-size: 18px;
  margin: 0 0 5px;
  color: #333;
}

.player-author {
  font-size: 14px;
  color: #666;
  margin-bottom: 15px;
}

.player-controls {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 15px;
}

.player-button {
  background: none;
  border: none;
  cursor: pointer;
}

.play-button {
  font-size: 32px;
}

.time {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  color: #666;
  margin-bottom: 10px;
}

.progress-bar {
  width: 100%;
  height: 5px;
  background: #eee;
  border-radius: 5px;
  margin-bottom: 15px;
}

.progress {
  height: 100%;
  background: #F5C563;
  border-radius: 5px;
}

.action-button {
  padding: 5px 10px;
  background: #F5C563;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-right: 10px;
}

.action-button:last-child {
  margin-right: 0;
}

.mini-player {
  position: fixed;
  bottom: 60px;
  left: 10px;
  right: 10px;
  background: white;
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: 1000;
}

.mini-player-cover {
  width: 50px;
  height: 50px;
  border-radius: 5px;
  margin-right: 10px;
}

.mini-player-info {
  flex: 1;
}

.mini-player-title {
  font-size: 14px;
  color: #333;
}

.mini-player-author {
  font-size: 12px;
  color: #666;
}

.mini-player-controls {
  display: flex;
  gap: 10px;
}

.pulse {
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

/* Модальные окна */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  border-radius: 10px;
  padding: 20px;
  width: 90%;
  max-width: 400px;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.modal-title {
  font-size: 18px;
  color: #333;
}

.modal-close-button {
  background: none;
  border: none;
  font-size: 16px;
  cursor: pointer;
  color: #666;
}

.modal-body {
  font-size: 14px;
  color: #333;
}

.payment-options {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin: 10px 0;
}

.payment-button {
  padding: 10px;
  background-color: #F5C563;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
}

.payment-button:hover {
  background-color: #E5989B;
}

/* Навигация */
.nav-bar {
  display: flex;
  justify-content: space-around;
  padding: 10px;
  background: #fff;
  border-top: 1px solid #eee;
}

.nav-button {
  background: none;
  border: none;
  color: #666;
  font-size: 12px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nav-button.active {
  color: #F5C563;
}

.nav-button svg {
  margin-bottom: 2px;
}